<template><section><html><head></head><body><h1>GeometryInstance</h1>
<p><code v-pre="">vc-instance-geometry</code> 组件用于加载几何体实例，通过几何体实例可以加载几何体。几何体实例可以将一个几何体对象放置在几个不同的位置并进行唯一着色。 例如，可以对一个 BoxGeometry 进行多次实例化，每次使用不同的 modelMatrix 来更改其位置，旋转和缩放比例。</p>
<h2>示例</h2>
<h3>加载几何体实例</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive :appearance="appearance" @click="clicked">
          <vc-instance-geometry id="top" :geometry="geometry" :color="color" :attributes="attributes" :model-matrix="modelMatrix1"></vc-instance-geometry>
          <vc-instance-geometry id="bottom" :geometry="geometry" :color="color" :attributes="attributes" :model-matrix="modelMatrix2"></vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearance2" :geometry-instances.sync="geometryInstances">
          <vc-instance-geometry>
            <vc-geometry-rectangle :rectangle="rectangle"></vc-geometry-rectangle>
          </vc-instance-geometry>
          <vc-instance-geometry :geometry.sync="geometry2">
            <vc-geometry-polygon :polygon-hierarchy="polygonHierarchy" :height="height"></vc-geometry-polygon>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
