        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = <span class="hljs-variable language_">this</span>.<span class="hljs-property">cesiumInstance</span>
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">appearance</span> = <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">PerInstanceColorAppearance</span>()
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">attributes</span> = {
          <span class="hljs-attr">color</span>: <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">ColorGeometryInstanceAttribute</span>.<span class="hljs-title function_">fromColor</span>(<span class="hljs-title class_">Cesium</span>.<span class="hljs-property">Color</span>.<span class="hljs-property">AQUA</span>)
        }
