<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>geometry</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定几何体实例加载的几何体对象。</td>
</tr>
<tr>
<td>modelMatrix</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定将几何体对象从模型坐标转换为世界坐标的模型矩阵。</td>
</tr>
<tr>
<td>id</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 <code v-pre="">Scene#pick</code> 方法返回的用户自定义信息.</td>
</tr>
<tr>
<td>attributes</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定几何体实例的属性信息。</td>
</tr>
</tbody>
</table>
<hr>
<p>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/GeometryInstance.html">GeometryInstance</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          appearance: {},
          geometry: {},
          color: {},
          attributes: {},
          modelMatrix1: {},
          modelMatrix2: {},
          appearance2: null,
          geometry2: null,
          geometryInstances: null,
          polygonHierarchy: [
            { lng: 102.1, lat: 29.5 },
            { lng: 106.2, lat: 29.5 },
            { lng: 106.2, lat: 33.5 },
            { lng: 108.2, lat: 35.5 },
            { lng: 102.1, lat: 33.5 }
          ],
          height: 200,
          rectangle: { west: 110.5, south: 29.5, east: 115.5, north: 34.5 }
        }
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const { Cesium, viewer } = this.cesiumInstance
          this.appearance = new Cesium.PerInstanceColorAppearance()
          this.attributes = {
            color: Cesium.ColorGeometryInstanceAttribute.fromColor(Cesium.Color.AQUA)
          }
          this.geometry = Cesium.BoxGeometry.fromDimensions({
            vertexFormat: Cesium.VertexFormat.POSITION_AND_NORMAL,
            dimensions: new Cesium.Cartesian3(1000000.0, 1000000.0, 500000.0)
          })

          this.modelMatrix1 = Cesium.Matrix4.multiplyByTranslation(
            Cesium.Transforms.eastNorthUpToFixedFrame(Cesium.Cartesian3.fromDegrees(108, 40.03883)),
            new Cesium.Cartesian3(0.0, 0.0, 100000.0),
            new Cesium.Matrix4()
          )
          this.modelMatrix2 = Cesium.Matrix4.multiplyByTranslation(
            Cesium.Transforms.eastNorthUpToFixedFrame(Cesium.Cartesian3.fromDegrees(108, 40.03883)),
            new Cesium.Cartesian3(0.0, 0.0, 1500000.0),
            new Cesium.Matrix4()
          ),
          this.appearance2 = new Cesium.MaterialAppearance({
            material: Cesium.Material.fromType('Checkerboard', {
              repeat: new Cesium.Cartesian2(20.0, 6.0)
            }),
            materialSupport: Cesium.MaterialAppearance.MaterialSupport.TEXTURED
          })
        },
        clicked (e) {
          console.log(e)
        }
      }
    }
  </script>